                <div class="ser_list_item">
                    <select v-model="selected">
                        <option v-for="option in options" v-bind:value="option.value">
                            {{ option.text }}
                        </option>
                    </select>
                </div>
            </li>
            <li>
                <h4></h4>
                <div class="ser_list_item">
                    
                    <input type="text" placeholder="">
                </div>
            </li>
            <li>
                <h4></h4>
                <div class="ser_list_item">
                    
                    <input type="text" placeholder="200">
                </div>
            </li>
            <photos></photos>
        </ul>
        <input id="upload_img" type="file" accept="image/gif,image/jpeg,image/png" hidden/>
        <button class="ui-btn-lg ui-btn-red" type="submit" v-link="{name:'negotiationResult2'}"></button>
    </div>
    
</template>

<script>
import photos from "../../components/photo";
    export default {
        //el : "#index_body",
        data () {
            return {
                selected: '',
                selectedWeixiu: '',
                open: this.$route.params.dataId==0?true:false,
                options: [
                  { text: '', value: ''},
                  { text: '', value: ''}
                ]
            };
        },
        components:{
            photos
        },
        init:function(){
            /**/  
            console.log('dataId: ' + this.$route.params.dataId); 
             
        },
        ready:function(){
        },
        watch: {
            selected: function(val) {
                this.open = val == "" ? true : false;
                var title= val == "" ? "" : "";
                $('header').find("h1").text(title)
            }
        },
        methods : {
        }
    };
</script>


