    <template>
        <div class="address address_add">
            <header class="ui-header">
                <i class="ui-icon-return" onclick="history.back()"></i>
                <h1></h1>
            </header>
            <section class="ui-container" v-cloak>
                <div class="address-add-list">
                    <div class="ui-form-item ui-border-b">
                        <label></label>
                        <input type="text" v-model="address.consignee" id="consignee" value="{{address.consignee}}" placeholder="">
                    </div>
                    <div class="ui-form-item ui-border-b">
                        <label></label>
                        <input type="text" v-model="address.mobile" id="mobile" value="{{address.mobile}}" placeholder="">
                    </div>
                    <div class="ui-form-item ui-border-b">
                        <label></label>
                        <input type="text" v-model="address.id_card" id="id_card" value="{{address.id_card}}" placeholder="">
                    </div>
                    <div class="ui-form-item ui-border-b">
                        <label></label>
                       <div class="ui-select-group">
                        </div>
                    </div>
                    <div class="ui-form-item ui-border-b">
                        <label></label>
                        <input type="text" v-model="address.address" id="address" value="{{address.address}}" placeholder="">
                    </div>
                </div>

                <div class="address-set">
                    <label class="ui-checkbox-s">
                        <input type="checkbox">
                    </label>
                    <span></span>
                </div>
                <div class="footer">
                    <a class="ui-btn-lg ui-btn-red" id="btnSave"></a>
                </div>            
            </section><!-- /.ui-container-->
        </div>
    </template>
<script>

    export default {
        data () {
            return {
                apiUrl:'v1/wap/address',
                address:{
                    'user_id':'',
                    'consignee':'',
                    'province':'',// 
                    'city':'',// 
                    'district':'',// 
                    'address':'',
                    'mobile':''
                },
                addressID:this.$route.params.id,
                validTipMsg:'',
                title: '',
                value: ['', '', ''],
                status:0,//tips
            }
        },
        ready (){
            //id
            this.getAddressDetail();
        },
        init (){

        },
        components: {
        },
        methods:{
            getAddressDetail (id){
                var self = this;
                if(self.addressID != '-1'){
                // 
                    self.$http.get(self.apiUrl+'/'+self.addressID, {}, {
                        emulateJSON: !0,
                        emulateHTTP: !0
                    }).then(function(e) {
                        var t = e.json();
                        self.$set('address',t);

                    }, function(e) {});
                }
                
            },
            postAddress () {
                let self = this;
                // 
                    self.$http.post(self.apiUrl, self.address).then((data) => {
                        self.$route.router.go({ name: 'address'});
                    }, (data) => {
                        //
                        let msg = data.data.message;
                    });
            },
        }
    }
</script>



