<template>
    <div class="service_body2">
        <header class="ui-header">
            <i class="ui-icon-return" onclick="history.back()"></i>
            <h1>{{title}}</h1>
        </header>
        <ul class="ser_list">
            <li>
                <h4> <span class="red">*</span></h4>
                <div class="ser_list_item">
                    <select v-model="selected">
                        <option v-for="option in options" v-bind:value="option.value">
                            {{ option.text }}
                        </option>
                    </select>
                </div>
            </li>
            <li v-show="open">
                <h4> <span class="red">*</span></h4>
                <div class="ser_list_item">
                    <select>
                        <option></option>
                        <option></option>
                        <option>//</option>
                        <option>//</option>
                        <option></option>
                        <option>/</option>
                        <option></option>
                    </select>
                </div>
            </li>
            <li>
                <h4>{{ selected }}</h4>
                <div class="ser_list_item">
                    <input type="text" placeholder="200">
                </div>
            </li>
            <photos></photos>
        </ul>
        <input id="upload_img" type="file" accept="image/gif,image/jpeg,image/png" hidden/>
        <button class="ui-btn-lg ui-btn-red" v-link="{name:'negotiation'}"></button>
    </div>
    
</template>

<script>
import photos from "../../components/photo";
    export default {
        //el : "#index_body",
        data () {
            return {
                title:'',
                selected: '',
                selectedWeixiu: '',
                open: true,
                options: [
                  { text: '', value: ''},
                  { text: '', value: ''}
                ]
            };
        },
        components:{
            photos
        },
        init:function(){
             
        },
        ready:function(){
        },
        watch: {
            selected: function(val) {
                this.open = val == "" ? true : false;
                this.title= val == "" ? "" : "";
            }
        },
        methods : {
        }
    };
</script>


